@media only screen and (max-width: 1100px) {
    body {
        margin: 10px 20px 10px 10px;
        overflow-x: hidden;
    }

    div#Navigation {
        background-image: initial;
        width: initial;
    }

    div.NaviLink, div.NaviBlock span {
        display: block;
        margin-bottom: 10px;
    }

    div.NaviBlock {
        display: table-cell;
    }

    div.NaviBlock a {
        white-space: nowrap;
    }

    div.NaviBox {
        margin: 10px 20px;
        float: left;
    }

    div.NaviSpacer {
        display: none;
    }

    span#activeLinkArrow {
        display: none;
    }

    div#Logo {
        display: none !important;
    }

    div#SeitenUeberschrift {
        background-position: right;
    }

    div#Inhalt {
        display: initial;
    }

    div.form {
        display: flow-root;
    }

    table.Liste {
        width: 1000%;
    }

    div.Buero {
        white-space: initial;
    }

    div.Bank,
    div.VertragNeu,
    div.MarktplatzNeu,
    div.MafiaNewAction,
    div.GroupCreate,
    div.GroupJoin,
    div.LoginForm,
    div.RegisterForm {
        width: fit-content;
    }

    div.GroupCreate {
        margin-bottom: 20px;
    }

    div#Footer {
        width: initial;
    }

    div.Gebaeude div.Action,
    div.Produktion div.Action,
    div.Research div.Action {
        display: inline-block;
        width: 210px
    }

    div.ResearchDaten,
    div.ProduktionDaten {
        width: 170px;
    }

    div.ResearchDaten div,
    div.ProduktionDaten div {
        text-align: initial !important;
    }

    div.Gebaeude div.Action {
        float: initial;
    }

    div.Gruppe div.GroupDescription {
        max-width: 300px;
    }

    div.Gruppe div.GroupImage img {
        max-height: 300px;
        max-width: 300px;
    }

    table.Nachrichten {
        white-space: initial;
    }

    /* when user is not logged in */
    div#Navigation.offline div.NaviBox {
        text-align: center;
    }

    div#Navigation.offline div.NaviBox header {
        display: none;
    }

    div#Navigation.offline div.NaviBox div {
        display: inline-block;
        margin: 6px;
    }
}
